import React, { useState } from "react";
import { Box, Text } from "../style";
import Circle from "./Circle";

const Footer = () => {
    const [size, setSize] = useState();
    const [position, setPositon] = useState(140);

    window.addEventListener("scroll", () => {
        let y = window.scrollY;
        if (y > 1000) {
            setSize(0);
        } else if (y > 875 || y > 1000) {
            setPositon(0);
        } else {
            setSize(375);
            setPositon(140);
        }
    });

    return (
        <Box
            height="60vh"
            display="flex"
            justify="center"
            style={{ marginTop: "5rem" }}
        >
            <Box size="90%" display="flex" align="center" direction="column">
                <Box size="90%" style={{ borderTop: "1px solid white" }}></Box>
                <Box
                    size="100%"
                    display="flex"
                    justify="center"
                    overflow="hidden"
                    height="68rem"
                    style={{ marginTop: "3rem", marginBottom: "3rem" }}
                >
                    <p
                        style={{
                            fontSize: "2.1em",
                            translate: `0 ${position}px`,
                            transition: "1s",
                        }}
                    >
                        "Try not to become a man of success, but rather try to
                        become a man of value."
                    </p>
                </Box>
                <Box size="90%" style={{ borderTop: "1px solid white" }}></Box>
                <a
                    className="end"
                    href="#intro"
                    style={{
                        zIndex: 99,
                        color: "white",
                        textDecoration: "none",
                    }}
                >
                    the end
                </a>
                <Circle y={-80} x={0} color="white" size={size} />
            </Box>
        </Box>
    );
};

export default Footer;
