import React, { useEffect, useState } from "react";
import axios from "axios";
import { useParams, Link } from "react-router-dom";
import { Box } from "../style";
import { IoMdArrowRoundBack } from "react-icons/io";

const api = "https://newsapi.org/v2/";
const key = "745a9d87fe874b21ac46671c7eb2f78b";

const ReadNews = ({ all }) => {
    let { id } = useParams();
    let { country } = useParams();
    const [datas, setDatas] = useState([]);

    const get = async () => {
        const { data } = await axios.get(
            `${api}top-headlines?country=${country}&apiKey=${key}`
        );
        setDatas(data.articles[id]);
    };

    const getAll = async () => {
        const { data } = await axios.get(
            `${api}everything?q=bitcoin&apiKey=${key}`
        );
        setDatas(data.articles[id]);
    };

    useEffect(() => {
        !all ? get() : getAll();
    }, []);

    return (
        <Box
            display="flex"
            align="center"
            direction="column"
            style={{ marginTop: "2rem" }}
        >
            <Box
                size="90%"
                border={true}
                radius="20px"
                padding="2rem"
                display="flex"
            >
                <Box>
                    <img
                        src={datas.urlToImage}
                        className="img"
                        alt="gambar tidak tersedia"
                    />
                </Box>
                <Box style={{ textAlign: "end" }} padding="1px">
                    <h1>{datas.title}</h1>
                    <p>{datas.description}</p>
                    <p>source: {datas.source && datas.source.name}</p>
                    <Box
                        display="flex"
                        justify="space-between"
                        style={{ paddingLeft: "2rem" }}
                    >
                        <p>author: {datas.author}</p>
                        <a href={datas.url} target="_blank">
                            <button className="btn">view more</button>
                        </a>
                    </Box>
                </Box>
            </Box>
            <Box size="90%">
                <Box>
                    <Box
                        display="flex"
                        justify="space-between"
                        style={{ paddingLeft: "2rem" }}
                    >
                        <Link to={all ? "/news" : `/news/${country}`}>
                            <button className="btn" style={{ height: "3rem" }}>
                                <span
                                    style={{
                                        display: "flex",
                                        alignItems: "center",
                                        marginLeft: "10px",
                                    }}
                                >
                                    <IoMdArrowRoundBack size={"1.5rem"} />{" "}
                                    <p style={{ textDecoration: "none" }}>
                                        Back
                                    </p>
                                </span>
                            </button>
                        </Link>
                    </Box>
                </Box>
            </Box>
        </Box>
    );
};

export default ReadNews;
