import React, { useState } from "react";
import { Box, Text } from "../style";
import ListCountry from "./ListCountry";

const Content = ({ getParams }) => {
    const [position, setPosition] = useState(-100);

    window.addEventListener("scroll", () => {
        let y = window.scrollY;
        if (y > 463) {
            setPosition(0);
        } else {
            setPosition(-100);
        }
    });

    return (
        <>
            <Box
                display="flex"
                justify="center"
                size="100%"
                direction="column"
                align="center"
                id="content"
                overflow="hidden"
            >
                <Box size="90%" style={{ borderTop: "1px solid white" }}></Box>
                <Box
                    height="100vh"
                    size="90%"
                    display="flex"
                    direction="column"
                    align="center"
                >
                    <Text
                        size="2.3rem"
                        transform="uppercase"
                        style={{
                            transition: "1s",
                            translate: `0 ${position}px `,
                        }}
                    >
                        news
                    </Text>
                    <ListCountry getParams={getParams} />
                </Box>
            </Box>
        </>
    );
};

export default Content;
