import React from "react";
import { Box, Card, Text } from "../style";
import { Link } from "react-router-dom";
import AOS from "aos";

AOS.init();

const countries = [
    {
        title: "indonesia",
        url: "/news/id",
    },
    {
        title: "amerika",
        url: "/news/us",
    },
    {
        title: "jepang",
        url: "/news/jp",
    },
    {
        title: "malaysia",
        url: "/news/my",
    },
];

const ListCountry = ({ getParams }) => {
    return (
        <>
            {countries.map((item, id) => {
                return (
                    <Box
                        size="100%"
                        display="flex"
                        key={id}
                        justify={id % 2 === 0 ? "end" : "start"}
                    >
                        <Card
                            data-aos="fade-up"
                            data-aos-anchor-placement="bottom-center"
                            width={"15%"}
                            height="6rem"
                            direction={id % 2 === 0 ? "row-reverse" : "row"}
                            radius="0px"
                            className="card"
                            style={{ cursor: "pointer" }}
                        >
                            <Box
                                display="flex"
                                direction="column"
                                align={id % 2 === 0 ? "end" : "start"}
                            >
                                <Text size="1.3em" transform="capitalize">
                                    {item.title}
                                </Text>
                                <Text
                                    size=".9em"
                                    transform="capitalize"
                                    style={{ translate: "0 -15px" }}
                                >
                                    all news about {item.title}
                                </Text>
                            </Box>
                            <Box
                                className="link"
                                style={{
                                    position: "absolute",
                                    transition: "1s",
                                    left: id % 2 === 0 ? "76%" : "22%",
                                }}
                            >
                                <Link
                                    to={`${item.url}`}
                                    style={{
                                        textDecoration: "none",
                                        color: "white",
                                    }}
                                    onClick={() => getParams(item.title)}
                                >
                                    go this way
                                </Link>
                            </Box>
                        </Card>
                    </Box>
                );
            })}
        </>
    );
};

export default ListCountry;
