import React from "react";
import { Box, Text, Btn } from "../style";
import ParticlesBg from "particles-bg";

const Intro = ({ y }) => {
    return (
        <>
            <Box
                id="intro"
                height="90vh"
                justify="center"
                align="center"
                display="flex"
                direction="column"
                overflow="hidden"
            >
                {/* <ParticlesBg
                    type="polygon"
                    bg={{
                        position: "absolute",
                        width: "100%",
                    }}
                    num={3}
                /> */}
                <Text
                    color="white"
                    size="2.5em"
                    weight="700"
                    style={{ translate: `0 ${y * 0.6}px` }}
                >
                    NEWS PORTAL
                </Text>
                <Text
                    color="white"
                    size="1.5em"
                    weight="500"
                    transform="capitalize"
                    style={{ translate: "0 -4rem" }}
                >
                    <span className="red">---</span> news internasional {""}
                    <span className="red">---</span>
                </Text>
                <a href="#content">
                    <Btn
                        bg="white"
                        size="10rem"
                        height="3rem"
                        style={{
                            fontSize: "20px",
                            fontWeight: 600,
                            textTransform: "uppercase",
                            translate: "0 -3rem",
                        }}
                    >
                        started
                    </Btn>
                </a>
            </Box>
        </>
    );
};

export default Intro;
