import React from "react";
import { Box, Card, Btn } from "../style";
import { Link } from "react-router-dom";

const News = ({ datas, search, country }) => {
    return (
        <Box>
            {datas &&
                datas
                    .filter((data) => {
                        if (search === "") {
                            return data;
                        } else if (
                            data.title
                                .toLowerCase()
                                .includes(search.toLowerCase())
                        ) {
                            return data;
                        }
                    })
                    .map((item, id) => {
                        return (
                            <Box
                                size="100%"
                                display="flex"
                                key={id}
                                style={{
                                    marginTop: "2rem",
                                    marginBottom: "2rem",
                                }}
                                justify={id % 2 === 0 ? "end" : "start"}
                            >
                                <Card
                                    data-aos="flip-down"
                                    // width={"15%"}
                                    hover="news"
                                    height="6rem"
                                    direction={
                                        id % 2 === 0 ? "row-reverse" : "row"
                                    }
                                    radius="0px"
                                    className="card"
                                    style={{
                                        cursor: "pointer",
                                        marginTop: "3rem",
                                    }}
                                >
                                    <Box
                                        display="flex"
                                        direction="column"
                                        align={id % 2 === 0 ? "end" : "start"}
                                        style={{
                                            textAlign:
                                                id % 2 === 0 ? "end" : "start",
                                        }}
                                    >
                                        <p>{item.title}</p>
                                    </Box>
                                    <Box
                                        background={`url(${item.urlToImage})`}
                                        className="news"
                                        display={"flex"}
                                        justify="center"
                                        align="center"
                                        style={{
                                            translate:
                                                id % 2 === 0
                                                    ? "1rem -3rem"
                                                    : "-1rem -3rem",
                                        }}
                                    >
                                        <Link
                                            to={
                                                !country
                                                    ? `/news/all/${id}`
                                                    : `/news/${country}/${id}`
                                            }
                                        >
                                            <Btn bg="blue" size={"4rem"}>
                                                <h3 style={{ color: "white" }}>
                                                    visit
                                                </h3>
                                            </Btn>
                                        </Link>
                                    </Box>
                                </Card>
                            </Box>
                        );
                    })}
        </Box>
    );
};

export default News;
