import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import { Box, Text } from "../style";
import News from "../components/News";

const api = "https://newsapi.org/v2/";
const key = "745a9d87fe874b21ac46671c7eb2f78b";

const Country = ({ title }) => {
    const [datas, setDatas] = useState([]);
    const [search, setSearch] = useState("");
    let { country } = useParams();
    const [x, setX] = useState();

    const getTitle = () => {
        if (!title) {
            if (country === "id") {
                setX("indonesia");
            }
            if (country === "my") {
                setX("malaysia");
            }
            if (country === "us") {
                setX("amerika");
            }
            if (country === "jp") {
                setX("jepang");
            }
        } else {
            return title;
        }
    };
    const get = async () => {
        const { data } = await axios.get(
            `${api}top-headlines?country=${country}&apiKey=${key}`
        );
        setDatas(data.articles);
    };

    useEffect(() => {
        get();
        getTitle();
    }, []);

    return (
        <div
            style={{
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
                marginTop: "3rem",
            }}
        >
            <Box size="90%" style={{ borderTop: "1px solid white" }}></Box>
            <Box size="90%">
                <Box
                    display="flex"
                    align="center"
                    direction="column"
                    overflow="hidden"
                >
                    <h1
                        style={{
                            textTransform: "uppercase",
                            animation: "up 1.5s forwards",
                        }}
                    >
                        hot news today
                    </h1>
                    <Box
                        size="90%"
                        style={{ borderTop: "1px solid white" }}
                    ></Box>
                </Box>
                <Box display="flex" justify="space-between" height="4rem">
                    <Text transform="capitalize" size="1.5rem">
                        <Link
                            to={"lmooNews/"}
                            style={{
                                textDecoration: "none",
                                color: "white",
                                animation: "opacity 2s ease-in forwards",
                            }}
                        >
                            {title ? title : x}
                        </Link>
                    </Text>
                    <Box display="flex" align="end">
                        <input
                            type={"text"}
                            onChange={(e) => setSearch(e.target.value)}
                            className="input"
                            placeholder="search..."
                        />
                    </Box>
                </Box>
                <News datas={datas} search={search} country={country} />
            </Box>
        </div>
    );
};

export default Country;
