import "./App.css";
import { useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import HomePage from "./pages/HomePage";
import Country from "./pages/Country";
import AOS from "aos";
import Navbar from "./components/Navbar";
import { Box, Container } from "./style";
import ReadNews from "./pages/ReadNews";
import AllNews from "./pages/AllNews";
import Test from "./pages/Test";
import MediaQuery from "react-responsive";

const themes = [
    {
        bg: "rgb(40,43,41)",
        color: "rgb(166,209,201)",
    },
    {
        bg: "#3C2317",
        color: "white",
    },
];

function App() {
    const [theme, setTheme] = useState(themes[0]);
    const [title, setTitle] = useState();

    const getParams = (title) => {
        setTitle(title);
    };

    return (
        <>
            <MediaQuery minWidth={884}>
                <Container
                    className="App"
                    background={theme.bg}
                    color={theme.color}
                    mHeight="100vh"
                >
                    <Router>
                        <Navbar mode={setTheme} themes={themes} />

                        <Routes>
                            <Route
                                path="/"
                                element={<HomePage getParams={getParams} />}
                            />
                            <Route path="/news" element={<AllNews />} />

                            <Route
                                path="/news/:country"
                                element={<Country title={title} />}
                            />
                            <Route
                                path="/news/all/:id"
                                element={<ReadNews all={true} />}
                            />
                            <Route
                                path="/news/:country/:id"
                                element={<ReadNews />}
                            />
                        </Routes>
                    </Router>
                </Container>
            </MediaQuery>
            <MediaQuery maxWidth={883}>
                <Container color="rgb(166,209,201)" background="rgb(40,43,41)">
                    <Box
                        height="100vh"
                        overflow="hidden"
                        display="flex"
                        justify="center"
                        align="center"
                    >
                        <h1 className="typing">coming soon ...</h1>
                    </Box>
                </Container>
            </MediaQuery>
        </>
    );
}

export default App;
