import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import { Box, Text } from "../style";
import News from "../components/News";

const api = "https://newsapi.org/v2/";
const key = "745a9d87fe874b21ac46671c7eb2f78b";

const AllNews = () => {
    const [datas, setDatas] = useState([]);
    const [search, setSearch] = useState("");
    const [x, setX] = useState();

    const get = async () => {
        const { data } = await axios.get(
            `${api}everything?q=bitcoin&apiKey=${key}`
        );
        setDatas(data.articles);
    };

    useEffect(() => {
        get();
    }, []);

    return (
        <div
            style={{
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
                marginTop: "3rem",
            }}
        >
            <Box size="90%" style={{ borderTop: "1px solid white" }}></Box>
            <Box size="90%">
                <Box display="flex" align="center" direction="column">
                    <h1 style={{ textTransform: "uppercase" }}>
                        hot news today
                    </h1>
                    <Box
                        size="90%"
                        style={{ borderTop: "1px solid white" }}
                    ></Box>
                </Box>
                <Box display="flex" justify="end" height="4rem">
                    <Box display="flex" align="end">
                        <input
                            type={"text"}
                            onChange={(e) => setSearch(e.target.value)}
                            className="input"
                            placeholder="search..."
                        />
                    </Box>
                </Box>
                <News datas={datas} search={search} />
            </Box>
        </div>
    );
};

export default AllNews;
