import React, { useRef, useState } from "react";
import { Box, Text } from "../style";
import { BsUnion } from "react-icons/bs";
import { Link } from "react-router-dom";

const Navbar = ({ mode, themes }) => {
    const dark = useRef();
    const light = useRef();
    const [y, setY] = useState(-100);

    const Switch = (e) => {
        if (e === 1) {
            light.current.style.translate = "0 -20px";
            dark.current.style.translate = "0 -23px";
            mode(themes[e]);
        } else if (e === 0) {
            light.current.style.translate = "0 0px";
            dark.current.style.translate = "0 0px";
            mode(themes[e]);
        }
    };

    window.addEventListener("scroll", () => {
        let y = window.scrollY;
        if (y > 462) {
            setY(0);
        } else {
            setY(-100);
        }
    });

    return (
        <div style={{ position: "sticky", top: 0, zIndex: 9 }}>
            <Box
                height="20px"
                display="flex"
                justify="space-between"
                padding="15px"
                align="center"
            >
                <Link
                    to={"/"}
                    style={{ textDecoration: "none", color: "white" }}
                >
                    <Text transform="uppercase" size="1.4em">
                        news portal
                    </Text>
                </Link>
                <Box size="15%" display="flex" justify="space-between">
                    <Link
                        style={{
                            textDecoration: "none",
                            color: "rgb(166,209,201)",
                        }}
                        to="/news"
                    >
                        <Text cursor="pointer" transform="capitalize">
                            news
                        </Text>
                    </Link>
                    <Text cursor="pointer" transform="capitalize">
                        about
                    </Text>
                    <Text cursor="pointer" transform="capitalize">
                        contact
                    </Text>
                </Box>
                <div
                    style={{
                        // border: "1px solid white",
                        maxHeight: "100%",
                        width: "4rem",
                        overflow: "hidden",
                    }}
                >
                    <Box
                        onClick={() => Switch(1)}
                        style={{
                            cursor: "pointer",
                            transition: "1s ease-out",
                        }}
                        ref={light}
                    >
                        <BsUnion color="white" size="1.2em" />
                    </Box>
                    <Box
                        onClick={() => Switch(0)}
                        style={{ cursor: "pointer", transition: "1s ease-out" }}
                        ref={dark}
                    >
                        <BsUnion color="blue" size="1.2em" />
                    </Box>
                </div>
            </Box>
            <Box display="flex" justify="center">
                <Box
                    size="90%"
                    style={{
                        borderTop: "1px solid white",
                        translate: `0 ${y}px`,
                        transition: "1s",
                    }}
                ></Box>
            </Box>
        </div>
    );
};

export default Navbar;
