import React from "react";
import { Circlee } from "../style";

const Circle = ({ x, y, color, size }) => {
    const styled = {
        translate: `${x}px ${y}px`,
        position: "relative",
    };

    return (
        <svg
            viewBox="0 0 100 100"
            xmlns="http://www.w3.org/2000/svg"
            style={styled}
        >
            <Circlee cx="50" cy="50" r="30" color={color} size={size} />
        </svg>
    );
};

export default Circle;
